.contact-container{
    margin: 4rem 0rem;
    position: relative;
}

.contact-container h5{
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 3rem;
}

.contact-content{
    display: flex;
    gap: 3rem;
}

.contact-content a{
    color: #fff;
    text-decoration: none;
}

@media (max-width: 768px){
    .contact-content{
        flex-direction: column-reverse;
    }

    .contact-container{
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }
}

