.nav-wrapper{
    background: rgba(0, 0, 0, 0.1);
    padding: 0.5rem 0;
    position: sticky;
    top: 0;
    z-index: 30;
    backdrop-filter: blur(50px);
}

.nav-content{
    max-width: 1300px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 0;
    margin: 0 auto;
}

.logo{
    width: 13rem;
    height: auto;
    cursor: pointer;
}

.nav-content ul{
    display: flex;
    align-items: center;
    gap: 0.5rem;
    list-style: none;
}

.nav-content li{
    margin: 0 1.5rem;
}

.menu-item{
    text-decoration: none;
    font-size: 0.9rem;
    font-weight: 500;
    color: #fff;
    position: relative;
    cursor: pointer;
}

.menu-item::before{
    content: "";
    width: 2rem;
    height: 0.2rem;
    background:linear-gradient(180deg, #45dbdba2, #5fb0e6 100%);
    border-radius: 0.5rem;
    position: absolute;
    bottom: -0.6rem;
    opacity: 0;
    transform: translateX(-1.5rem);
    transition: all 0.3s ease;
}

.menu-item:hover::before{
    width: 100%;
    transform: translateX(0);
    opacity: 1;
}

.contact-btn{
    font-size: 0.9rem;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background:linear-gradient(180deg, #45dbdba2, #5fb0e6 100%);
    padding: 0.6rem 2rem;
    border: none;
    outline: 1.5px soli transparent;
    border-radius: 0.2rem;
    cursor: pointer;
    transition: all 0.3 ease;
}

.contact-btn a{
    text-decoration: none;
}

.contact-btn:hover{
    color: azure;
    background: black;
    outline: 1.5px solid azure;
}

.menu-btn{
    width: 2.2rem;
    height: 2.2rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 0.4rem;
    color: #fff;
    background: linear-gradient(180deg, #45dbdba2, #5fb0e6 100%);
    line-height: 0;
    cursor: pointer;
    transition: all 0.4s ease;
    display: none;
}

.menu-btn:hover{
    color: #a993fe;
    background: black;
    outline: 1.5px solid #a993fe;
}

@media (max-width: 425px){
    .logo{
        width: 10rem;
        height: auto;
    }
}

@media (max-width: 769px) {
    .menu-btn{
        display: block;
    }

    .nav-content ul{
        display: none;
    }
}

@media (max-width: 1325px){
    .nav-wrapper{
        padding: 0 2rem;
    }
}

